import React from 'react';
import "./NewsCard.css"
import {Accordion} from "react-bootstrap";

 export const NewsCard = ({banner,name,des}) => {
    return (
        <div>
            <div className="news-card mt-3 d-flex justify-content-between">
                <div className="col-5">
                    <img className="card-img" src={banner}/>
                </div>

                <div className='col-6'>
                    <div className="card-name">{name.toUpperCase()}</div>
                    <div className="accordion mt-3">
                        <Accordion defaultActiveKey="1">
                            <Accordion.Item eventKey="0">
                                <Accordion.Header className="accordion-title">Читать полностью</Accordion.Header>
                                <Accordion.Body>
                                    {des}
                                </Accordion.Body>
                            </Accordion.Item>
                        </Accordion>
                    </div>
                </div>

            </div>
        </div>
    );
};

;
